.modal-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: var(--modal-backdrop-color);
    backdrop-filter: blur(var(--modal-backdrop-blur));
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2000;
}

.modal-container {
    background: var(--modal-surface);
    width: 90%;
    max-width: 400px;
    border-radius: var(--modal-radius);
    padding: 24px;
    color: white;
    border: var(--modal-border);
    box-shadow: var(--modal-shadow);
    max-height: 85vh;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.modal-container.events-mode {
    background: var(--modal-surface);
}

@media (max-width: 768px) {
    .modal-container {
        width: 100%;
        max-width: 100%;
        height: 100%;
        max-height: 100vh;
        border-radius: 0;
        padding: 24px 32px 80px 32px;
    }

    .profile-content {
        padding: 0 8px;
    }
}

.modal-header {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 24px;
    padding-top: 2px;
    position: relative;
}

.modal-header h2 {
    margin: 0;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.2;
    text-align: center;
}

.yellow-title {
    color: var(--color-accent) !important;
}

@media (max-width: 768px) {
    .modal-header {
        justify-content: center;
        gap: 16px;
    }

    .modal-header h2 {
        text-align: center;
        flex: 0 1 auto;
    }

    .header-back-btn {
        display: inline-flex !important;
    }
}

.close-btn {
    background: none;
    border: none;
    color: #fff;
    cursor: pointer;
    padding: 4px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.close-btn:hover {
    background: rgba(255, 255, 255, 0.1);
}


.profile-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
}

.avatar-container {
    position: relative;
    width: 100px;
    height: 100px;
}

.profile-image,
.profile-placeholder {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
}

.profile-placeholder {
    background: linear-gradient(135deg, #FFB800, #FFDC73);
    color: #1a1a1a;
    font-size: 36px;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
}

.user-name {
    margin: 0;
    font-size: 22px;
    font-weight: 600;
}

.user-email {
    margin: 0;
    color: #888;
    font-size: 14px;
}

.user-description {
    margin: 8px 0 0 0;
    color: #e0e0e0;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.4;
    max-width: 90%;
    opacity: 0.9;
    text-align: center;
}

.follow-actions {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin: 6px 0 12px;
}

.report-btn {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    background: rgba(255, 255, 255, 0.06);
    border: 1px solid rgba(255, 255, 255, 0.2);
    color: #ff8b8b;
    padding: 8px 14px;
    border-radius: 18px;
    cursor: pointer;
    font-size: 13px;
}

.report-btn mat-icon {
    font-size: 18px;
    width: 18px;
    height: 18px;
}

.report-btn:hover {
    border-color: rgba(255, 139, 139, 0.6);
}

.stats-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    gap: 0;
    
    margin-top: 16px;
    background: rgba(255, 255, 255, 0.05);
    padding: 16px 12px;
    
    border-radius: 12px;
    width: 100%;
}

.stat-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4px;
    flex: 1;
    
}

.stat-item.clickable {
    cursor: pointer;
}

.stat-item.clickable:hover {
    opacity: 0.8;
}

.stat-value {
    font-size: 18px;
    font-weight: 700;
    color: #FFB800;
}

.stat-label {
    font-size: 11px;
    
    color: #888;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.stat-divider {
    width: 1px;
    height: 30px;
    
    background: rgba(255, 255, 255, 0.1);
    flex-shrink: 0;
    
}


.follow-main-btn {
    width: 100%;
    max-width: 220px;
    margin: 0 auto;
    color: #ffffff !important;
}


.follow-main-btn:not(.following) {
    background-color: var(--color-accent);
}

.follow-main-btn.following {
    background: rgba(255, 255, 255, 0.1);
    color: #ffffff;
    border: 1px solid rgba(255, 255, 255, 0.2);
}

.bell-btn {
    width: 44px;
    height: 44px;
    border-radius: 12px;
    border: 1px solid rgba(255, 255, 255, 0.15);
    background: rgba(255, 255, 255, 0.06);
    color: #f1f1f1;
    display: grid;
    place-items: center;
    cursor: pointer;
    transition: background-color 0.2s ease, border-color 0.2s ease, transform 0.1s ease;
}

.bell-btn:hover {
    background: rgba(255, 255, 255, 0.12);
}

.bell-btn:active {
    transform: translateY(1px);
}

.bell-btn.active {
    border-color: var(--color-accent);
    background: rgba(251, 188, 4, 0.18);
    color: var(--color-accent);
}

.bell-btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
}


.events-view {
    display: flex;
    flex-direction: column;
    gap: 12px;
    overflow-y: auto;
    padding-right: 4px;
    scrollbar-width: thin;
    scrollbar-color: var(--color-bit-gray) transparent;
    max-height: 500px;
    
}

.events-section {
    background: rgba(255, 255, 255, 0.04);
    padding: 12px;
    border-radius: 14px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    border: 1px solid rgba(255, 255, 255, 0.08);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}


.events-view::-webkit-scrollbar {
    width: 6px;
}

.events-view::-webkit-scrollbar-track {
    background: transparent;
}

.events-view::-webkit-scrollbar-thumb {
    background-color: var(--color-bit-gray);
    border-radius: 20px;
}

.events-section h4 {
    margin: 0;
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    color: #cfcfcf;
}

.event-card {
    background: rgba(255, 255, 255, 0.06);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 14px;
    padding: 12px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    box-shadow: 0 6px 16px rgba(0, 0, 0, 0.3);
}

.event-card.clickable {
    cursor: pointer;
    transition: border-color 0.2s ease, background-color 0.2s ease, transform 0.1s ease;
}

.event-card.clickable:hover {
    border-color: rgba(252, 195, 36, 0.4);
    background: rgba(255, 255, 255, 0.08);
}

.event-card.clickable:active {
    transform: translateY(1px);
}

.event-card.past {
    opacity: 0.85;
}

.event-card-header {
    display: flex;
    flex-direction: column;
    gap: 6px;
}

.event-title {
    margin: 0;
    font-size: 18px;
    font-weight: 800;
    color: #fdfdfd;
}

.event-sub {
    margin: 2px 0 0 0;
    font-size: 13px;
    color: #d6d6d6;
    display: flex;
    align-items: center;
    gap: 8px;
}

.event-sub .inline-icon {
    color: var(--color-accent);
    font-size: 18px;
    width: 18px;
    height: 18px;
    line-height: 18px;
    flex-shrink: 0;
}

.event-stats {
    display: flex;
    flex-wrap: wrap;
    gap: 6px;
    align-items: center;
}

.stat-pill {
    border-color: rgba(255, 255, 255, 0.15);
    background: rgba(255, 255, 255, 0.08);
    color: #f5f5f5;
    font-size: 12px;
    padding: 7px 9px;
    gap: 5px;
    border-radius: 999px;
    display: inline-flex;
    align-items: center;
}

.stat-pill mat-icon {
    font-size: 16px;
    width: 16px;
    height: 16px;
    line-height: 16px;
    color: var(--color-accent);
    flex-shrink: 0;
}


.list-view {
    display: flex;
    flex-direction: column;
    flex: 1;
    overflow: hidden;
    
}

.list-header {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 16px;
}

.back-btn {
    background: none;
    border: none;
    color: white;
    cursor: pointer;
    padding: 4px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 36px;
    height: 36px;
    line-height: 0;
}

.header-back-btn {
    position: absolute;
    left: 12px;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
}

.modal-header .close-btn {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
}

.user-list {
    flex: 1;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    gap: 12px;
    padding-right: 4px;
}


.user-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 8px;
}

.user-info {
    display: flex;
    align-items: center;
    gap: 12px;
    cursor: pointer;
}

.list-avatar,
.list-avatar-placeholder {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.list-avatar-placeholder {
    background: #FFB800;
    color: #1a1a1a;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 14px;
}

.list-username {
    font-weight: 500;
    font-size: 14px;
}

.small-btn {
    padding: 6px 12px;
    border-radius: 16px;
    font-size: 12px;
    font-weight: 600;
    cursor: pointer;
    border: none;
}

.small-btn.follow-btn {
    background: #FFB800;
    color: #1a1a1a;
}

.small-btn.unfollow-btn {
    background: rgba(255, 255, 255, 0.1);
    color: white;
}

.loading-spinner,
.error-state,
.empty-list {
    text-align: center;
    color: #888;
    padding: 20px;
}
